.header {
  max-width: 800px;
  margin: 0px auto;
  padding: 15px 20px;

  display: flex;
  justify-content: space-between;
  -webkit-box-pack: justify;
  align-items: center;

  > div {
    width: auto;
    flex: 1 1 50%;
    display: flex;
    align-items: center;
  }
}

.header__logo {
  a {
    font-size: 24px;
    color: white;
    transition: 50ms ease-in-out;

    &:hover {
      opacity: 0.8;
    }
  }
}

.header__links {
  justify-content: flex-end;

  ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;

    li {
      display: inline-block;
      margin: 0px 15px;

      a {
        color: #838383;
        font-size: 18px;

        &:hover {
          text-decoration: underline;
        }
      }

      &:nth-last-of-type(1) {
        margin-right: 0px;
      }
    }
  }
}

.header__links_active {
  color: white !important;
}

.footer {
  padding: 20px 0px;
  text-align: center;

  p {
    font-size: 16px;
    color: #838383;

    > a {
      text-decoration: underline;
      color: #838383;
    }
  }
}

#title {
  text-align:center;
  font-size: 5rem;
  margin: 15px 0 0;
}

.prefix__prefix__base {
    fill: #fff;
    font-family: serif;
    font-size: 12px
}

.bag {
  display: inline-block;
  vertical-align: top;
  margin: 15px;
  color: white;

  &:hover {
    opacity: 0.8;
  }
}

.home__feature {
  padding-top: 40px;
  text-align: center;

  > span {
    display: block;
    color: #8e8e8e;
    font-size: 18px;
    padding: 10px 0px;
  }
}

.home__function {
  button {
    font-size: 16px;
    font-family: "EB Garamond", serif, sans-serif;
    color: white;
    margin: 10px;
    height: 36px;
    background-color: #1a1a1a;
    width: 100px;
    border: solid 1px;
    border-radius: 5px;
  }
  input {
    font-size: 14px;
    text-align: center;
    font-family: "EB Garamond", serif, sans-serif;
    color: white;
    margin: 10px;
    height: 30px;
    background-color: #1a1a1a;
    width: 200px;
    border: solid 1px;
    border-radius: 5px;
  }
  ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;

    li {
      display: inline-block;
      margin: 0px 5px;

      a {
        color: #838383;
        font-size: 18px;
        &.selected {
          color: yellow;
        }
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

.bag_attributes {
  background-color: black;
  width: 400px;
  height: 400px;
  position: relative;

  > span {
    position: absolute;
    right: 10px;
    top: 10px;
  }
  ul {
      list-style-type: none;
      margin: 0px;
      padding: 20px;
      text-align: left;

      li {
        margin: 5px 0px;
        &.sritem {
          color: yellow;
        }

      span {
        font-size: 20px;
      }
    }
  }
}
